.todo-search {
    background: #F9FBFC;
    border-radius: 2px;
    border: 2px solid #202329;
    margin: 0 24px;
    height: 64px;
    width: calc(100% - 62px);
    font-size: 24px;
    text-align: center;
    font-family: 'Mukto' sans-serif;
    font-weight: 400;
    color: #1E1E1F;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
}
.todo-search::placeholder {
    color: #a5a5a5;
    font-family: 'Mukto' sans-serif;
    font-weight: 400;
}
.todo-search:focus {
    outline-color: cyan;
}